import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import './App.css';
import { HeaderComponent, FooterComponent, HomePage, PricingPage, TeamPage, FeedbackPage, DashboardPage, LoginPage, RegPage } from './Components';

export default function MainRouter() {
  return (
    <Router>
      <HeaderComponent/>
      <Switch>
        <Route exact path="/start-it-front" render = { () => <HomePage/>} />
        <Route exact path="/start-it-front/pricing" render = { () => <PricingPage/>}/>
        <Route exact path="/start-it-front/team" render = { () => <TeamPage/>}/>
        <Route exact path="/start-it-front/feedback" render = { () => <FeedbackPage/>}/>
        <Route exact path="/start-it-front/dashboard" render = { () => <DashboardPage/>}/>
        <Route exact path="/start-it-front/login" render = { () => <LoginPage/>}/>
        <Route exact path="/start-it-front/join" render = { () => <RegPage/>}/>
      </Switch>
      <FooterComponent/>
    </Router>
  );
}